.club-card {
    position: relative;
    &__group {
        position: relative;

        &:nth-child(3) {
            margin: 20px 0;
        }

        &--slider {
            margin: 20px 0;

            @media (min-width: $md) {
                margin: 0;
            }
        }

        @media (min-width: $md) {
            margin-bottom: 20px;
        }
    }


    &__name {
        margin-bottom: 0;
        @media (max-width: 1080px) {
            margin-bottom: 20px;
        }
        @media (max-width: $md) {
            margin-bottom: 10px;
        }
    }

    &__image {
        @media (max-width: $md) {
            & img {
                width: 100%;
            }
        }
    }

    .club-card__bonus--width-60 {
        width: auto;
    }

    &__welcome {
        margin-bottom: 25px;
        @media(max-width: 1090px) {
            margin-bottom: 17px;
        }
    }

    &__btn {
        position: static;
        margin-top: 20px;
        margin-bottom: 40px;

        @media(max-width: $md) {
            margin-bottom: 20px;
        }

        button {
            width: 20%;

            @media(max-width: $lgx) {
                width: 270px;
            }

            @media(max-width: $md) {
                width: 100%;
            }
        }
    }

    &__wrap--center {
        display: -webkit-box;
        display: -webkit-flex;
        display: -moz-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-align: center;
        -webkit-align-items: center;
           -moz-box-align: center;
            -ms-flex-align: center;
                align-items: center;
                @media(max-width: 1090px) {
                    -webkit-box-orient: vertical;
                    -webkit-box-direction: normal;
                    -webkit-flex-direction: column;
                       -moz-box-orient: vertical;
                       -moz-box-direction: normal;
                        -ms-flex-direction: column;
                            flex-direction: column;
                    -webkit-box-align: start;
                    -webkit-align-items: flex-start;
                       -moz-box-align: start;
                        -ms-flex-align: start;
                            align-items: flex-start;
                }
    }



    &__wrap {
        display: -webkit-box;
        display: -webkit-flex;
        display: -moz-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-pack: justify;
        -webkit-justify-content: space-between;
           -moz-box-pack: justify;
            -ms-flex-pack: justify;
                justify-content: space-between;
                -webkit-box-align: start;
                -webkit-align-items: flex-start;
                   -moz-box-align: start;
                    -ms-flex-align: start;
                        align-items: flex-start;
                margin-bottom: 30px;
                @media(max-width: $lgx) {
                    -webkit-box-align: center;
                    -webkit-align-items: center;
                       -moz-box-align: center;
                        -ms-flex-align: center;
                            align-items: center;
                }
                .club-card__box-left {
                    width: 100%;
                    margin: 0;
                }
                .club-card__bonus {
                    width: auto;
                    @media(max-width: $lg) {
                        width: 100%;
                        margin-top: -15px;
                    }
                    @media(max-width: $md) {
                        width: 60%;
                        margin-top: 0;
                    }
                }
                .club-card__status-text {
                    text-align: left;
                    margin-bottom: 25px;
                    @media(max-width: $lgx) {
                        margin-bottom: -3px;
                        font-size: 14px;
                    }
                    @media(max-width: $md) {
                        display: block;
                        margin-bottom: 10px;
                        font-size: 11px;
                    }
                }
                .club-card__box {
                    margin-left: 65px;
                    -webkit-box-orient: vertical;
                    -webkit-box-direction: normal;
                    -webkit-flex-direction: column;
                       -moz-box-orient: vertical;
                       -moz-box-direction: normal;
                        -ms-flex-direction: column;
                            flex-direction: column;
                            width: 50%;

                    @media(max-width: $lgx) {
                        margin-left: 25px;
                        width: 55%;
                        .step__badge span {
                            font-size: 24px;
                        }
                    }
                    @media(max-width: 870px) {
                        width: 58%;
                        margin-left: 0;
                    }
                    @media(max-width: 520px) {
                        width: 100%;
                    }
                }
                .wrap {
                    display: -webkit-box;
                    display: -webkit-flex;
                    display: -moz-box;
                    display: -ms-flexbox;
                    display: flex;
                    margin-top: 50px;

                    @media(max-width: $lgx) {
                        margin-top: 10px;
                        -webkit-box-align: center;
                        -webkit-align-items: center;
                           -moz-box-align: center;
                            -ms-flex-align: center;
                                align-items: center;
                    }

                    @media(max-width: 1090px) {
                        position: absolute;
                        bottom: 0;
                        left: 0;
                    }
                    @media(max-width: 705px) {
                        -webkit-box-orient: vertical;
                        -webkit-box-direction: normal;
                        -webkit-flex-direction: column;
                           -moz-box-orient: vertical;
                           -moz-box-direction: normal;
                            -ms-flex-direction: column;
                                flex-direction: column;
                    }
                    @media(max-width: 520px) {
                        position: static;
                        margin-top: 0;
                    }
                    .step__badge {
                        width: 305px;
                        font-size: 16px;
                        padding: 5px 22px;
                        height: 60px;
                        br {
                            @media(max-width: 1090px) {
                                display: none;
                            }
                            @media(max-width: 520px) {
                                display: block;
                            }
                        }
                        @media(max-width: 1210px) {
                            width: 390px;
                        }
                        @media(max-width: 1090px) {
                            width: 60%;
                            font-size: 13px;
                            display: -webkit-box;
                            display: -webkit-flex;
                            display: -moz-box;
                            display: -ms-flexbox;
                            display: flex;
                            -webkit-box-align: center;
                            -webkit-align-items: center;
                               -moz-box-align: center;
                                -ms-flex-align: center;
                                    align-items: center;
                            margin: 0;
                        }
                        @media(max-width: $md) {
                            display: block;
                            width: 100%;
                            padding: 5px 15px;
                            font-size: 10px;
                            height: 50px;
                            span {
                                font-size: 14px;
                            }
                        }

                    }
                }
                .step__button-block {
                    width: 276px;
                    margin-right: 35px;
                    @media(max-width: $lgx) {
                        width: 290px;
                    }
                    @media(max-width: 1090px) {
                        width: 320px;
                        margin-right: 5px;
                    }
                    @media(max-width: 520px) {
                        width: 100%;
                        margin-right: 0;
                    }
                }

            .club-card__img-card {
                width: 44%;
                @media(max-width: $lgx) {
                    width: 460px;
                }
                @media(max-width: 1090px) {
                    width: 40%;
                }
                @media(max-width: 520px) {
                    width: 100%;
                    margin-bottom: 10px;
                }
            }

            @media(max-width: 1090px) {
                position: relative;
                padding-bottom: 80px;
            }
            @media(max-width: 890px) {
                -webkit-box-align: start;
                -webkit-align-items: flex-start;
                   -moz-box-align: start;
                    -ms-flex-align: start;
                        align-items: flex-start;
            }
            @media(max-width: 705px) {
                padding-bottom: 120px;
            }
            @media(max-width: 520px) {
                -webkit-box-orient: vertical;
                -webkit-box-direction: normal;
                -webkit-flex-direction: column;
                   -moz-box-orient: vertical;
                   -moz-box-direction: normal;
                    -ms-flex-direction: column;
                        flex-direction: column;
                        padding-bottom: 0;
            }
    }



    &__badge {
        background: #f61d2a;
        color: #fff;
        padding: 16px 0;
        margin: 0 auto 0 40px;
        -webkit-border-radius: 12px;
           -moz-border-radius: 12px;
                border-radius: 12px;
        width: 380px;
        font-size: 24px;
        display: -webkit-box;
        display: -webkit-flex;
        display: -moz-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-align: end;
        -webkit-align-items: flex-end;
           -moz-box-align: end;
            -ms-flex-align: end;
                align-items: flex-end;
        -webkit-box-pack: center;
        -webkit-justify-content: center;
           -moz-box-pack: center;
            -ms-flex-pack: center;
                justify-content: center;
                span {
                    color: #fff;
                    line-height: 1em;
                    @media(max-width: 1090px) {
                        font-size: 24px;
                    }

                    @media(max-width: 520px) {
                        font-size: 16px;
                    }
                }

                @media(max-width: 1300px) {
                    margin: 0 auto 0 30px;
                }

                @media(max-width: 1090px) {
                    margin-left: 0;
                    line-height: 1em;
                    margin-bottom: 15px;
                    width: 275px;
                    font-size: 17px;
                    height: 50px;
                }

                @media(max-width: $md) {
                    padding: 8px 0;
                    font-size: 14px;
                    height: 35px;
                }

                @media(max-width: 520px) {
                    width: 100%;
                    padding: 10px 0;
                }
    }

